.card {
    background: #fff;
    border-radius: 2px;
    height: 500px;
    position: relative;
    width: 325px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    padding-bottom: 50px;
    margin-left: 10px !important;
    margin-right: 10px !important;
    margin-top: 10px !important;
  }

  .buyCards {
    display: -ms-flexbox;
    display: flex;
  }
  
  .card > .img-container {
    height: 60%;
    overflow: hidden;
    text-align: center;
    background:#fff;
    padding-top: 20px;
  }
  .card > .img-container > img {
    width: 60%
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }



