.gamecontainer{
  height: 600px;
  width: 87%;
  margin: auto;
  border-radius: 20px;
  background-color: rgb(71, 132, 173);

}
.gamename{
  padding: 10px;
  margin: 15px;
  color: white;
  font-family: 'Arvo', serif;
}
.gamename > h4 {
  float: right;
  padding-right: 20vh;
}
.game {
  display: -ms-inline-flexbox;
  display: inline-flex; 
  -ms-flex-direction: column; 
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center; 
  width: 100%;
  padding: 20px;
}
.target {
  border: thin solid #999; 
  width: 50%; 
  height: 75px;
  font-size: 45px; 
  text-align: center; 
  display: inline-block;
  background-color: #ffffff;
  border-radius: 15px;
}
.challenge-numbers {
  width: 100%; 
  margin: auto;
  text-align: center;
  padding: 10px
}
.number {
  border: thin solid lightgray; 
  background-color: #eee;
  width: 40%; 
  text-align: center; 
  font-size: 36px;
  border-radius: 5px; 
  margin: auto;
  display: inline-block;
}
.footer {
  display: -ms-flexbox;
  display: flex; 
  width: 90%; 
  -ms-flex-pack: justify; 
      justify-content: space-between;  
}
.gamebutton {
  width: 85%;
  text-align: center;
  margin: auto;
  height: 35px;
  border-radius: 15px;
  background-color: #63D2FF;
}

.timer-value { 
  color: rgb(44, 197, 44); 
  font-size: 40px; 
}




